<template>
  <div class="api">
  	<h2>Audio API</h2>
  	<div class="api-info">
  		<h3>Audio API</h3>
  		<p><strong>Audio API</strong> - specification describes a high-level JavaScript API for processing and synthesizing audio in web applications.</p>
  	</div>
	
	<div class="api-info">
		<h3>The "audio" tag</h3>
	</div>
	<pre class="api-code">
	&lt;audio id="audio" controls preload&gt;&lt;source src="audio.mp3" /&gt;&lt;source src="audio.ogg" /&gt;&lt;/audio&gt; // the audio tag 
	</pre>
	<h4 class="api-table-caption">The Methods</h4>
	<table class="api-table">
		<tr>
			<td>play()</td>
			<td>This method will play or resume the media file.</td>
		</tr>
		<tr>
			<td>pause()</td>
			<td>This method will pause playback.</td>
		</tr>
		<tr>
			<td>load()</td>
			<td>This method loads the media file for dynamic applications.</td>
		</tr>
		<tr>
			<td>canPlayType(type)</td>
			<td>This method will indicate whether a file format is supported by the browser or not. It returns an empty string if the browser can’t play the media, and the strings “maybe” or “probably” based on how confident it is that it can be played.</td>
		</tr>
	</table>

	<pre class="api-code">
	var audio = new Audio("audio.mp3"); // or var audio = new Audio(); 
	audio.setAttribute("src", "audio.mp3");

	var play = document.getElementById('play');
	play.addEventListener('click', function(){ 
	   audio.play();
	}, false);

	var pause = document.getElementById('pause');
	pause.addEventListener('click', function(){ 
	   audio.pause();
	}, false);

	audio.volume = value; // value is float 
	audio.canPlayType('audio/mp3');
	</pre>
	<h4 class="api-table-caption">The Properties</h4>
	<table class="api-table">
		<tr>
			<td>paused</td>
			<td>It returns true if the media is currently paused or it hasn’t started playing.</td>
		</tr>
		<tr>
			<td>ended</td>
			<td>It returns true if the media has reached the end.</td>
		</tr>
		<tr>
			<td>duration</td>
			<td>It returns the duration of the media in seconds.</td>
		</tr>
		<tr>
			<td>currentTime</td>
			<td>This is a property that can return and receive a value to inform the position in which the media is being played or sets a new position to start playing.</td>
		</tr>
		<tr>
			<td>error</td>
			<td>This returns the error value if an error has occurred.</td>
		</tr>
		<tr>
			<td>buffered</td>
			<td>This offers information about how much the file has loaded so far into the buffer. It returns an array containing data about every portion of the media that has been downloaded.</td>
		</tr>
	</table>

	<pre class="api-code">
	audio.currentTime = 0; 
	if(audio.ended){
	     // the media file has finished playing 
	}
	</pre>
	
	<h4 class="api-table-caption">The Events</h4>
	<table class="api-table">
		<tr>
			<td>progress</td>
			<td>This event is fired periodically to inform of the progress downloading the media.</td>
		</tr>
		<tr>
			<td>canplaythrough</td>
			<td>This event is fired when the entire media can be played without interruption.</td>
		</tr>
		<tr>
			<td>canplay</td>
			<td>This event is fired when the media can be played. Unlike the previous, it’s fired when there are just a couple of frames available.</td>
		</tr>
		<tr>
			<td>ended</td>
			<td>This event is fired when the media reaches the end.</td>
		</tr>
		<tr>
			<td>pause</td>
			<td>This event is fired when the playback is paused.</td>
		</tr>
		<tr>
			<td>play</td>
			<td>This event is fired when the media starts playing.</td>
		</tr>
		<tr>
			<td>error</td>
			<td>This event is fired when an error occurs. The event is delivered to the element, if present, corresponding to the media source that produces the error.</td>
		</tr>
	</table>
	
	<pre class="api-code">
	var audio = new Audio("audio.mp3"); 
	audio.addEventListener('play', onPlay, false);
	audio.addEventListener('ended', onEnded, false);
	audio.addEventListener('progress', onProgress, false);
	</pre>
	<h4 class="api-table-caption">The Attributes</h4>
	<table class="api-table">
		<tr>
			<td>src</td>
			<td>the path of the file to be played.</td>
		</tr>
		<tr>
			<td>controls</td>
			<td>This activates the interface provided by default for every browser.</td>
		</tr>
		<tr>
			<td>autoplay</td>
			<td>When this attribute is present, the browser will automatically play the audio as soon as it can.</td>
		</tr>
		<tr>
			<td>loop</td>
			<td>If this attribute is specified, the browser will restart the audio upon reaching the end.</td>
		</tr>
		<tr>
			<td>preload</td>
			<td>This attribute can take three values: none, metadata or auto. The first one indicates that the audio shouldn’t be cached, usually with the goal of minimizing unnecessary traffic. The second value, metadata, will recommend the browser to fetch some information about the resource (for example, duration). The third value, auto, is the value set by default and it prompts the browser to download the file as soon as possible.</td>
		</tr>
	</table>
  </div>
</template>
<script>
export default {
  name: 'audio',
  data () {
  	return {}
  }
}
</script>



